body {
  margin: 0;
  font-family: Lato;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height:100%;
  overflow: hidden;
}

html {
  height:100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height:100%;
}

.contentPane {
    color: #333;
    width:100%;
    height: inherit;
    overflow: hidden;
}
.content {
    overflow: hidden;
    height: 100%;
    width: inherit;
    background-color: whitesmoke;
}
.MainMenuItem {
    font-size: 80%;
    display: inline-flex;
    flex-direction: column;
    width: 65px;
    height: 60px;
    min-height: 60px;
    padding: 0 4px 0 2px;
    justify-content: center;
    cursor: pointer;
    webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
    margin: 1px 0;
    letter-spacing: 0.05em;
}
.MainMenuNotSelectedItem:hover {
    opacity: .25;
    background: aliceblue;
    color: black;
    border-radius: 7px 0 0 7px;
}

.MainMenuNotSelectedItem {
    opacity: .5;
    padding-right: 6px;
}

.MainMenuSelectedItem {
    opacity: 1;
    /*background: aliceblue;*/
    border-radius: 7px 0 0 7px;
    color: black;
    background: linear-gradient(45deg, #ef6c00 30%, #e65100 90%);
    /*border-right: 3px solid #e65100;*/
    animation-name: fadeInOpacity;
    animation-iteration-count: 1;
    animation-timing-function: ease-in;
    animation-duration: 0.3s;
}

.MainMenuItem DIV {
    justify-content: center;
    display: flex;
}



.MainMenu {
    display: flex;
    flex-direction: column;
    color: white;
    background: rgb(0, 105, 192);
