.Card {
    display: flex;
    align-items: center;
    width:100%;
    background: white;
    border-bottom: 1px solid #ddd;
    cursor: pointer;
    max-height: 50px;
    animation-name: cardVisible;
    animation-iteration-count: 1;
    animation-timing-function: ease-in;
    animation-duration: 0.5s;
}

@keyframes cardVisible {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.Card:hover {
    background: #EEE;
    color: black;
}

.Card .scope {
    margin: 0 5px;
    width: 100px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.Card SPAN.timestamp {
    font-size: 90%;
}
.Card SPAN.timestamp,
.Card SPAN.scope {
    font-weight: 300;
}

.Card SPAN.timestamp,
.Card SPAN.scope,
.Card SPAN.type {
    flex: 1 1;
    padding: 0 4px;
}

.Card SPAN.name {
    flex: 2 1;
