    box-shadow: 0px 0px 17px -4px rgba(0,0,0,0.75);
    animation-name: splashVisible;
    animation-iteration-count: 1;
    animation-timing-function: ease-out;
    animation-duration: .8s;
}

@keyframes splashVisible {
    0% {
